@import "../setup";

.Main{
    margin-top: 10vh;
    h1{
        font-family: $croboto;
        font-size: 30px;
        font-weight: 400;
        margin: 0px;
        padding: 0px;
        text-align: center;
    }
    h2{
        @extend h1;
        font-family: $bebas;
        font-size: 36px;
    }
    p{
        text-align: left;
    }
    span{
        font-family: $mono;
        font-size: 24px;
    }
    .container{
        width: 100%;
        margin: auto;
        max-width: 1000px;
        display: flex;
        justify-content: space-around;
        .presentation{
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            height: 50vh;
            p{
                margin: 0px;
                max-width: 350px;
            }
            .cursor{
                animation-name: parpadeo;
                animation-duration: 1s;
                animation-iteration-count: infinite;
            }
            @keyframes parpadeo {
                0%{
                    opacity: 1;
                }
                100%{
                    opacity: 0;
                }
            }
        }
    }
    .Slider{
        width: 50%;
        height: 50vh;
        background-size: cover;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: ease 2s;
        .buttons{
            display: flex;
            justify-content: space-between;
            span{
                cursor: pointer; 
                display: flex;
                justify-content: center;
                align-items: center;
                background: rgba(0, 0, 0, 0.5);
                backdrop-filter: blur(6px);
                width: 20px;
                height: 64px;
                &:hover{
                    opacity: 0.6;
                }
            }
        }
        p{
            margin: 0px;
            width: 100%;
            height: 43px;
            display: flex;
            align-items: center;
            padding: 0px 20px;
            box-sizing: border-box;
            font-family: $roboto;
            font-size: 18px;
            color: white;
            background: rgba(0, 0, 0, 0.5);
            backdrop-filter: blur(6px);
        }
    }
    button{
        max-width: 320px;
        width: 70%;
        height: 50px;
        border-radius: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $roboto;
        color: white;
        font-size: 24px;
        margin: 5vh auto;
        cursor: pointer;
        border: none;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        &:hover{
            opacity: 0.7;
        }
    }
}

@include mediaQ($smallScreen){
    .Main{
        margin-top: 2vh;
        .container{
            flex-direction: column;
            align-items: center;
            .presentation{
                height: 25vh;
                h1, h2, p{
                    font-size: 25px;
                }
                p{
                    text-align: center;
                    span{
                        font-size: 20px;
                    }
                }
            }
        }
        .Slider{
            width: 100%;
            height: 40vh;
        }
    }
}

.dark{
    h1, h2{
        color: white;
    }
    span{
        color: $lightGreen;
    }
    button{
        background-image: linear-gradient($lightGreen, $darkGreen);
    }
}

.light{
    h1, h2{
        color: black;
    }
    span{
        color: $lightBlue;
    }
    button{
        background-image: linear-gradient($lightBlue, $darkBlue);
    }
}